$imgUrl: '../img/';

@mixin font-size-map($font-size-map) {
    @each $breakpoint, $font-size in $font-size-map {
        @if $breakpoint == null {
            font-size: $font-size;
        } @else {
	        @media (max-width: $breakpoint) {
	            font-size: $font-size;
	        }
        }
    }
}
$html-font-size: (null:16px, 768px:14px, 360px:12px);

